:root {
  --hover-color: #00acdc;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Roboto", Helvetica, sans-serif;
  font-weight: 500;
  font-size: 12px;
  line-height: 14px;
}

.header {
  padding: 0 50px;
  display: flex;
  background: #fff;
  justify-content: space-between;
  box-shadow: 0 2px 4px #f2f2f2;
}

.logo {
  position: absolute;
  top: 10px;
  left: 50px;
}

.nav__list {
  display: flex;
  list-style: none;
  justify-content: space-between;
  margin: 0;
  padding: 0;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  text-decoration: none;
  color: #000;
  padding: 23px 0;
  text-transform: uppercase;
  display: block;
  cursor: pointer;
}

.nav__link:hover,
.is-active {
  color: var(--hover-color);
}

.is-active {
  position: relative;
}

.is-active::after {
  content: "";
  display: block;
  background-color: var(--hover-color);
  height: 4px;
  border-radius: 8px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}
